.bottom-overlay__container {
  position: relative;
  width: 50%;
}

.bottom-overlay__image {
  display: block;
  width: 100%;
  height: auto;
}

.bottom-overlay__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #008cba;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: 0.5s ease;
}

.bottom-overlay__container:hover .bottom-overlay__overlay {
  height: 100%;
}

.text {
  color: #f1faee;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  font-family: "Montserrat", sans-serif;
}
